type Store = {
  currentPage: number;
  feeds: NewsFeed[];
};

type News = {
  id: number;
  time_ago: string;
  title: string;
  url: string;
  user: string;
  content: string;
};

type NewsFeed = News & {
  comments_count: number;
  points: number;
  read?: boolean; // optional
};

type NewsDetail = News & {
  comments: NewsComment[];
};

type NewsComment = News & {
  comments: NewsComment[];
  level: number;
};

const container: HTMLElement | null = document.getElementById("root");
const ajax: XMLHttpRequest = new XMLHttpRequest();
const NEWS_URL = "https://api.hnpwa.com/v0/news/1.json";
const CONTENT_URL = "https://api.hnpwa.com/v0/item/@id.json";

// Ï§ëÎ≥µÎêú ÏΩîÎìúÎ•º Ï§ÑÏù¥Í≥† Ìï®ÏàòÎ•º ÎßåÎì§Ïñ¥ ÌõÑÏóê Î≥ÄÍ≤ΩÍ≥º Ï∂îÏ†ÅÏù¥ ÏâΩÎèÑÎ°ù ÏΩîÎìúÎ•º ÏûëÏÑ±Ìï† Ïàò ÏûàÎèÑÎ°ù ÌïòÏûê.

// currentPage Ï†ÑÏó≠ Î≥ÄÏàòÎ°ú ÏÑ§Ï†ïÌïòÏó¨ ÎèåÏïÑÍ∞ÄÍ∏∞ ÎàÑÎ•º Ïãú Ìï¥Îãπ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
// feedsÎ•º Î∞∞Ïó¥Î°ú ÏÑ†Ïñ∏, Ïó¨Îü¨Î≤àÏùò getDataÌï®Ïàò Ìò∏Ï∂úÏùÑ ÌöåÌîºÌïòÍ∏∞ ÏúÑÌï¥ Î∞∞Ïó¥ Î≥ÄÏàòÎ•º ÏÑ†Ïñ∏ÌïòÏó¨ Ï†ÄÏû•

const store: Store = {
  currentPage: 1,
  feeds: [],
};

// URL data Î∂àÎü¨Ïò§Í∏∞
function getData<AjaxResponse>(url: string): AjaxResponse {
  ajax.open("GET", url, false);
  ajax.send();

  return JSON.parse(ajax.response);
}

// ÏùΩÏùÄ Í∏ÄÏùÄ ÌëúÏãúÌï¥Ï£ºÍ∏∞  white => yellow
// Í∏Ä Î™©Î°ù Ï†ÑÏ≤¥ ÏùΩÏùå Ï≤òÎ¶¨ Îã§ false Î°ú Ï¥àÍ∏∞Ìôî ÏãúÏºúÏ£ºÍ∏∞
function makeFeeds(feeds: NewsFeed[]): NewsFeed[] {
  for (let i = 0; i < feeds.length; i++) {
    feeds[i].read = false;
  }
  return feeds;
}

function updateView(html: string): void {
  if (container) {
    container.innerHTML = html;
  } else {
    console.error("ÏµúÏÉÅÏúÑ Ïª®ÌÖåÏù¥ÎÑàÍ∞Ä ÏóÜÏñ¥ UIÎ•º ÏßÑÌñâÌïòÏßÄ Î™ªÌï©ÎãàÎã§.");
  }
}
// Í∏Ä Î™©Î°ù Ìï®Ïàò
function newsFeed(): void {
  let newsFeed: NewsFeed[] = store.feeds;
  const newsList = [];
  // template Î≥ÄÏàòÎ•º ÏÑ†Ïñ∏ÌïòÏó¨ ÎààÏúºÎ°ú ÏãùÎ≥ÑÌïòÍ∏∞ ÏâΩÎèÑÎ°ù html ÌòïÏãùÏúºÎ°ú Íµ¨ÌòÑ Î∞è Í∞íÏùÑ Ï†ÑÎã¨ÌïòÏó¨ containerÏóê Ï†ÄÏû•ÌïòÏó¨ ÌôîÎ©¥Ïóê ÎùÑÏö∞Í∏∞
  let template = `
  <div class="bg-gray-600 min-h-screen">
  <div class="bg-white text-xl">
    <div class="mx-auto px-4">
      <div class="flex justify-between items-center py-6">
        <div class="flex justify-start">
          <h1 class="font-extrabold">Hacker News</h1>
        </div>
        <div class="items-center justify-end">
          <a href="#/page/{{__prev_page__}}" class="text-gray-500">
            Previous
          </a>
          <a href="#/page/{{__next_page__}}" class="text-gray-500 ml-4">
            Next
          </a>
        </div>
      </div> 
    </div>
  </div>
  <div class="p-4 text-2xl text-gray-700">
    {{__news_feed__}}        
  </div>
</div>
  `;
  // getDataÎ•º ÌïúÎ≤àÎßå Ìò∏Ï∂úÌïòÏó¨ Î∂àÌïÑÏöîÌïú Îç∞Ïù¥ÌÑ∞ ÌÜµÏã† Ï§ÑÏù¥Í∏∞
  if (newsFeed.length === 0) {
    newsFeed = store.feeds = makeFeeds(getData<NewsFeed[]>(NEWS_URL));
  }

  // ÌéòÏù¥ÏßÄ ÎÑ§Ïù¥ÏÖò
  for (let i = store.currentPage - 1; i < store.currentPage * 10; i++) {
    newsList.push(
      `    
      <div class="p-6 ${
        newsFeed[i].read ? "bg-yellow-500" : "bg-white"
      } mt-6 rounded-lg shadow-md transition-colors duration-500 hover:bg-green-100">
      <div class="flex">
        <div class="flex-auto">
          <a href="#/show/${newsFeed[i].id}">${newsFeed[i].title}</a>  
        </div>
        <div class="text-center text-sm">
          <div class="w-10 text-white bg-green-300 rounded-lg px-0 py-2">${
            newsFeed[i].comments_count
          }</div>
        </div>
      </div>
      <div class="flex mt-3">
        <div class="grid grid-cols-3 text-sm text-gray-500">
          <div><i class="fas fa-user mr-1"></i>${newsFeed[i].user}</div>
          <div><i class="fas fa-heart mr-1"></i>${newsFeed[i].points}</div>
          <div><i class="far fa-clock mr-1"></i>${newsFeed[i].time_ago}</div>
        </div>  
      </div>
    </div>    
  `
    );
  }

  // replace Ìï®ÏàòÎ•º ÌÜµÌï¥ Í∞íÏù¥ Îì§Ïñ¥Í∞ÄÎäî Î∂ÄÎ∂ÑÏùÑ Î∞îÍøîÏ§ÄÎã§.
  // Ïù¥Îü∞ÏãùÏùò ÏΩîÎìú Íµ¨ÌòÑÏùÄ ÏûêÏπ´ ÏΩîÎìúÏñëÏù¥ ÎäòÏñ¥ÎÇ®Ïóê Îî∞Îùº ÏΩîÎìúÍ∞Ä Î≥µÏû°Ìï¥Î≥¥Ïù¥Îäî Îã®Ï†êÏù¥ Ï°¥Ïû¨ÌïúÎã§.
  template = template.replace("{{__news_feed__}}", newsList.join(""));

  // if Ï°∞Í±¥Ï†àÏùÑ ÏÇ¨Ïö©ÌïòÍ∏∞ Î∂ÄÎã¥Ïä§Îü¨Ïö∏ ÎïåÎäî ÏÇºÌï≠Ïó∞ÏÇ∞ÏûêÎ•º ÌôúÏö©ÌïòÏó¨ Í∞ÑÎã®ÌïòÍ≤å Ï°∞Í±¥Ï†àÏùÑ Ï§Ñ Í≤É
  template = template.replace(
    "{{__prev_page__}}",
    String(store.currentPage > 1 ? store.currentPage - 1 : 1)
  );

  template = template.replace(
    "{{__next_page__}}",
    String(store.currentPage < 3 ? store.currentPage + 1 : 3)
  );

  updateView(template);
}

// Í∏Ä ÏÑ∏Î∂Ä ÎÇ¥Ïö©
function newsDetail(): void {
  const id = location.hash.substr(7);

  const newsContent = getData<NewsDetail>(CONTENT_URL.replace("@id", id));
  let template = `
  <div class="bg-gray-600 min-h-screen pb-8">
  <div class="bg-white text-xl">
    <div class="mx-auto px-4">
      <div class="flex justify-between items-center py-6">
        <div class="flex justify-start">
          <h1 class="font-extrabold">Hacker News</h1>
        </div>
        <div class="items-center justify-end">
          <a href="#/page/${store.currentPage}" class="text-gray-500">
            <i class="fa fa-times"></i>
          </a>
        </div>
      </div>
    </div>
  </div>

  <div class="h-full border rounded-xl bg-white m-6 p-4 ">
    <h2>${newsContent.title}</h2>
    <div class="text-gray-400 h-20">
      ${newsContent.content}
    </div>

    {{__comments__}}

  </div>
</div>
    `;

  // id Í∞íÏùÄ string Ïù¥Í∏∞ ÎïåÎ¨∏Ïóê NumberÎ°ú Ìòï Î≥ÄÌôò
  for (let i = 0; i < store.feeds.length; i++) {
    // Îì§Ïñ¥Í∞ÄÎäî Í∏ÄÏùò read Î≥ÄÏàòÏùò boolean Í∞íÏùÑ trueÎ°ú Î≥ÄÌôòÌïòÏó¨ 'ÏùΩÏùå' ÌëúÏãú
    if (store.feeds[i].id === Number(id)) {
      store.feeds[i].read = true;
      // Ï°∞Í±¥Í∞íÏùÑ ÎßåÏ°±ÌïòÍ≥† Î≥ÄÏàò Í∞íÏùÑ Ï¥àÍ∏∞Ìôî ÌõÑÏóê break Î¨∏ÏúºÎ°ú Ìï¥Îãπ Ï°∞Í±¥Ï†àÏùÑ Îπ†Ï†∏ÎÇòÏò®Îã§.
      break;
    }
  }
  updateView(
    template.replace("{{__comments__}}", makeComment(newsContent.comments))
  );
}

// Ïû¨Í∑Ä Ìò∏Ï∂úÏùÑ ÌÜµÌï¥ ÎåÄÎåìÍ∏Ä Î≥¥Ïó¨Ï£ºÍ∏∞
// called propsÎäî ÎåÄÎåìÍ∏ÄÏùò depthÎ•º ÏùòÎØ∏ÌïúÎã§.
function makeComment(comments: NewsComment[]): string {
  const commentString = [];

  for (let i = 0; i < comments.length; i++) {
    // Ï§ëÎ≥µ Î∞©ÏßÄ
    const comment: NewsComment = comments[i];

    commentString.push(`
          <div style="padding-left: ${comment.level * 40}px;" class="mt-4">
            <div class="text-gray-400">
              <i class="fa fa-sort-up mr-2"></i>
              <strong>${comment.user}</strong> ${comment.time_ago}
            </div>
            <p class="text-gray-700">${comment.content}</p>
          </div>      
        `);

    // ÎåìÍ∏Ä Î∞ëÏóê ÎåÄÎåìÍ∏ÄÏù¥ Ï°¥Ïû¨ÌïòÎ©¥ ÎßåÏ°±ÌïòÎäî Ï°∞Í±¥Ï†à
    if (comments[i].comments.length > 0) {
      commentString.push(makeComment(comment.comments));
    }
  }

  return commentString.join("");
}

// ÌôîÎ©¥ Ï†ÑÌôò router
function router(): void {
  const routePath = location.hash;
  // routerÏóêÏÑú # ÏùÄ ÎπàÍ∞íÏúºÎ°ú Ïù∏ÏãùÌïòÍ∏∞ ÎïåÎ¨∏Ïóê # Îí§Ïóê Í∞íÏù¥ ÏóÜÏùÑ Ïãú "" Îπà Í∞íÏùÑ Î∞òÌôòÌïúÎã§.
  if (routePath === "") {
    newsFeed();
  } else if (routePath.indexOf("#/page/") >= 0) {
    // #/page/@@ => currentPage Í∞íÏùÑ @@ Í∞íÏúºÎ°ú Ï¥àÍ∏∞ÌôîÌïúÎã§.
    store.currentPage = Number(routePath.substr(7));
    newsFeed();
  } else {
    // Í∏Ä ÏÉÅÏÑ∏Îäî #/show/
    newsDetail();
  }
}

// hashchange EventÍ∞Ä ÏÉùÍ∏∏ Ïãú router Ìï®ÏàòÎ•º Ìò∏Ï∂ú
window.addEventListener("hashchange", router);

// ÏµúÏ¥à 1Ìöå router Ìï®Ïàò Ìò∏Ï∂ú
router();
